import request from 'utils/request';
export default {
  list(options) {
    return request('/client/article/list', options, { method: 'GET' });
  },
  detail(options) {
    return request('/client/article/detail', options, { method: 'GET' });
  },
  start(options) {
    return request('/client/article/start', options);
  },
};
