import React, { useEffect, useState, useCallback } from 'react';
import { Spin } from 'components/antd';
import api from 'api';
import Tabs from 'components/Tabs';
import { getUrlParams } from 'utils';
import styles from './index.module.scss';
import ArticleItem from './ArticleItem';
import { Article } from 'models/index';
import Pagination from 'components/Pagination';
import LinkWrapper from 'components/LinkWrapper';
const TabsItem = [
  { name: 'javascript', key: '0' },
  { name: 'css', key: '1' },
  { name: 'react', key: '2' },
];
export default () => {
  const [pagination, setPagination] = useState({
    pageSize: 10,
    pageNum: 1,
  });
  const [dataSource, setDataSource] = useState<Array<Article>>([]);
  const [loading, setLoading] = useState(false);
  const [dataTotal, setDataTotal] = useState(0);
  const [activeKey, setActiveKey] = useState('0');
  const { pageSize, pageNum } = pagination;
  const articleCategory = getUrlParams('articleCategory');
  useEffect(() => {
    if (articleCategory) {
      setActiveKey(articleCategory);
    }
  }, [articleCategory]);
  //获取列表
  const fetchList = useCallback(async () => {
    setLoading(true);
    const { data, code } = await api.article.list({
      pageSize,
      pageNum,
      activeKey,
    });
    setLoading(false);
    if (code === 10000) {
      console.log(data.list);
      setDataSource(data.list);
      setDataTotal(data.total);
    }
  }, [pageSize, pageNum, activeKey]);
  const tabsChange = (v) => {
    setActiveKey(v);
  };
  const paginationChange = (pageNum) => {
    setPagination((prev) => ({ ...prev, pageNum }));
  };
  useEffect(() => {
    fetchList();
  }, [fetchList]);
  return (
    <div className={styles['article-wrapper']}>
      <div className={styles['article']}>
        <Tabs
          onChange={tabsChange}
          childrens={TabsItem}
          type="line"
          activeKey={activeKey}
        />
        <Spin spinning={loading}>
          <div className={styles['article-container']}>
            {dataSource.map((data) => (
              <LinkWrapper
                key={data._id}
                to={`/article/detail?id=${data._id}&articleCategory=${data.category}`}
              >
                <ArticleItem data={data} />
              </LinkWrapper>
            ))}
            <Pagination
              onChange={paginationChange}
              pageSize={pageSize}
              pageNum={pageNum}
              total={dataTotal}
            />
          </div>
        </Spin>
      </div>
      <div className={styles['sidebar']}>sidebar</div>
    </div>
  );
};
