import React from 'react';
import { Tabs } from 'components/antd';
import { TabsProps } from 'antd/lib/tabs';
const { TabPane } = Tabs;
type Child = Array<{ name: string; key: string }>;
interface IProps extends TabsProps {
  childrens: Child;
}
const TabsCom = ({ onChange, type, activeKey, childrens }: IProps) => (
  <Tabs onChange={onChange} type={type} activeKey={activeKey}>
    {childrens.map((item) => (
      <TabPane tab={item.name} key={item.key} />
    ))}
  </Tabs>
);
TabsCom.defaultProps = {
  type: 'card',
};
export default TabsCom;
