import { stringify } from 'qs';
//@ts-ignore
const noop = (_) => _;

const config = {
  $urlGetter: noop,
  $tokenGetter: noop,
  $beforeHook: noop,
  $afterHook: noop,
  $dataHook: noop,
  $errorHook: noop,
  $tokenInvalid: noop,
  url(func) {
    this.$urlGetter = func;
    return this;
  },
  token(func) {
    this.$tokenGetter = func;
    return this;
  },
  before(func) {
    this.$beforeHook = func;
    return this;
  },
  after(func) {
    this.$afterHook = func;
    return this;
  },
  data(func) {
    this.$dataHook = func;
    return this;
  },
  error(func) {
    this.$errorHook = func;
    return this;
  },
  tokenInvalid(func) {
    this.$tokenInvalid = func;
    return this;
  },
};
interface Option {
  withCredentials?: boolean;
  method?: string;
}
function checkStatus(response, isBlob) {
  if (response.status >= 200 && response.status < 300) {
    const currentUrl = response.url;
    const p = isBlob ? response.blob() : response.json();
    return p.then((resp) => {
      if (process.env.NODE_ENV === 'development') {
        console.log(currentUrl, resp);
      }
      return new Promise((resolve) => {
        // if (resp && resp.code === 10000) {
        //   // const url = response.url.replace(window.location.origin, '');
        // } else {
        //   if (resp && TOKEN_ERROR_CODE.includes(resp.code)) {
        //     //@ts-ignore
        //     config.$tokenInvalid();
        //   } else if (!isBlob) {
        //     message.error(resp.message || resp.msg);
        //   }
        // }
        isBlob
          ? resolve({
              code: 10000,
              data: resp,
            })
          : resolve(resp);
      });
    });
  } else {
    return Promise.resolve({
      data: null,
      code: response.status,
      message: 'error',
    });
  }
}

function request_fetch(url, params, options: Option = {}, isBlob = false) {
  let { method = 'POST' } = options;
  url = '/api' + url;

  // @ts-ignore
  config.$beforeHook(url, params, options);
  let targetUrl = config.$urlGetter(url);
  let o = {
    credentials: 'same-origin',
    method,
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8',
      'Cache-Control': 'no-cache',
      Pragma: 'no-cache',
    },
    mode: 'cors',
  };
  if (method === 'POST') {
    // application/json;charset=UTF-8;
    o.headers['Content-Type'] = 'application/json;charset=UTF-8';
    //@ts-ignore;
    o.body = JSON.stringify(params);
  }
  // if (withCredentials) {
  //   o.credentials = 'include';
  // }
  if (process.env.NODE_ENV !== 'production') {
  }
  if (method === 'GET' && params && JSON.stringify(params) !== '{}') {
    targetUrl += `?${stringify(params)}`;
  }
  console.log('targetUrl', targetUrl);
  console.log('0o', o);
  //@ts-ignore
  return fetch(targetUrl, o)
    .then((res) => checkStatus(res, isBlob))
    .catch((e) => {
      return Promise.resolve({
        data: null,
        code: 0,
        message: 'error',
      });
    });
}
let request = request_fetch;

export { config };
export default request;
