import copy from 'copy-to-clipboard';
import { message } from 'components/antd';
// 获取url参数
function getUrlParams(field: string): string;
function getUrlParams(fields: Array<string>): Array<string>;
function getUrlParams(field: any): any {
  let queryString = window.location.search.substring(1);
  let result = {};
  queryString
    .split('&')
    .filter((v) => v)
    .forEach((item) => {
      let key = item.split('=')[0];
      let value = item.split('=')[1];
      result[key] = value;
    });
  if (typeof field === 'string') {
    return result[field] || '';
  } else {
    for (let key in result) {
      if (field.indexOf(key) === -1) delete result[key];
    }
    return Object.values(result);
  }
}
export { getUrlParams };
//显示markdown图片宽/高
export const limitImgSize = (max = 300) => {
  const imgList = document.querySelectorAll('.markdown-container img');
  for (let i = 0; i < imgList.length; i++) {
    let imgItem = imgList[i] as HTMLImageElement;
    imgItem.style.display = 'block';
    imgItem.style.margin = '0 auto';
    if (imgItem.offsetHeight > imgItem.offsetWidth) {
      imgItem.style.height = max + 'px';
      imgItem.style.width = 'auto';
    } else {
      imgItem.style.width = max + 'px';
      imgItem.style.height = 'auto';
    }
  }
};
//给所有代码片段添加copy按钮
export const insertCopyIcon = () => {
  const preTags = document.querySelectorAll('.markdown-container pre');
  for (let i = 0; i < preTags.length; i++) {
    let btn = document.createElement('div');
    btn.className = 'copy-btn';
    btn.title = '点击复制代码';
    let preTag = preTags[i] as HTMLElement;
    preTag.style.position = 'relative';
    preTag.appendChild(btn);
    preTag.addEventListener('click', clickCopyIcon, false);
  }
};
//点击copy按钮
export const clickCopyIcon = (e) => {
  if (e.target.className === 'copy-btn') {
    let text = e.target.parentNode.firstElementChild.innerText;
    copy(text);
    message.success('复制代码成功');
  }
};
//过滤值为 null undefined '' 的字段
export const filterNull = (obj) => {
  const list = ['', undefined, null];
  for (let key in obj) {
    if (list.indexOf(obj[key]) !== -1) {
      delete obj[key];
    }
  }
  return obj;
};
//防抖函数
export const debounce = (func, time = 500) => {
  let timer = null;
  return () => {
    if (timer) {
      clearTimeout(timer);
    }
    timer = setTimeout(() => {
      func();
    }, time);
  };
};
//或缺star到end 的随机整数
export const getIntegerRandom = (start: number, end: number): number => {
  return Math.round(start + Math.random() * (end - start));
};
