import React, { useState, useEffect } from 'react';
import { Menu, Icon, Avatar, Button, Modal } from 'components/antd';
import classNames from 'classnames';
import styles from './index.module.scss';
import avatarImg from 'assets/images/avatar.jpg';
import wxqrcode from 'assets/images/wxqrcode.jpg';
import { useHistory, useLocation } from 'react-router-dom';
export default () => {
  const [selectedKeys, setSelectedKeys] = useState('/article');
  const [visible, setVisible] = useState(false);
  const history = useHistory();
  const location = useLocation();
  useEffect(() => {
    let key = '/' + location.pathname.split('/')[1];
    setSelectedKeys(key);
  }, [location.pathname]);
  const handleClick = (e) => {
    const path = e.key;
    history.push(path);
  };
  const handleCancel = () => {
    setVisible(false);
  };
  const showWxModal = () => {
    setVisible(true);
  };
  return (
    <div className={styles.wrapper}>
      <div className={classNames('center-box', styles.header)}>
        <div className={styles.avatar}>
          <Avatar src={avatarImg} />
        </div>
        <div className={styles.menu}>
          <Menu
            onClick={handleClick}
            selectedKeys={[selectedKeys]}
            mode="horizontal"
          >
            <Menu.Item key="/article">
              <Icon type="book" />
              文章
            </Menu.Item>
            <Menu.Item key="/music">
              <Icon type="customer-service" />
              音乐
            </Menu.Item>
            {/* <Menu.Item key="app" disabled>
              <Icon type="appstore" />
              Navigation Two
            </Menu.Item> */}
          </Menu>
        </div>
        <div className={styles.connect}>
          <Button type="link" onClick={showWxModal}>
            <Icon type="wechat" />
            联系我
          </Button>
        </div>
      </div>
      <Modal
        width={450}
        title="扫微信-联系我"
        visible={visible}
        onCancel={handleCancel}
        footer={null}
      >
        <img src={wxqrcode} style={{ width: 400 }} alt="-" />
      </Modal>
    </div>
  );
};
