import React, { useState, useEffect } from 'react';
interface IProps extends React.ImgHTMLAttributes<HTMLImageElement> {}
const ImageCom = (props: IProps) => {
  const [realSrc, setRealSrc] = useState(
    'https://via.placeholder.com/150x120?text=%E5%8A%A0%E8%BD%BD'
  );
  const { src, alt } = props;
  useEffect(() => {
    var img = new Image();
    img.src = src;
    img.onload = () => {
      setRealSrc(src);
    };
  }, [src]);
  return src && <img src={realSrc} alt={alt} />;
};
ImageCom.defaultProps = {
  alt: '-',
};
export default ImageCom;
