import React from 'react';
import styles from './index.module.scss';
import { Article } from 'models/index';
import { UTCtoDateString } from 'utils/date';
import Image from 'components/Image/index';
import { Tag } from 'components/antd';
import { tagColor } from 'const/maps';
import { getIntegerRandom } from 'utils/index';
import { ReactComponent as Good } from 'assets/icon/good.svg';
import { ReactComponent as View } from 'assets/icon/view.svg';
interface IProps {
  data: Article;
}
export default ({ data }: IProps) => {
  const { title, summary, logoUrl, views, starts, date, keyword } = data;
  let keywordArr = [];
  if (keyword) {
    keywordArr = keyword.split('/').filter((v) => v);
  }
  return (
    <div className={styles['article-item']}>
      <div className={styles['article-item-content']}>
        <div className={styles['article-item-content-title']}>{title}</div>
        <div className={styles['article-item-content-desc']}>{summary}</div>
        <div className={styles['article-item-content-info']}>
          <div className={styles['article-item-views']}>
            <View /> <span>{views}</span>
          </div>
          <div className={styles['article-item-zan']}>
            <Good /> <span>{starts}</span>
          </div>
          <div className={styles['article-item-date']}>
            {UTCtoDateString(date)}
          </div>
          <div>
            {keywordArr.map((tag) => {
              let index = getIntegerRandom(0, tagColor.length - 1);
              let color = tagColor[index];
              return (
                <Tag color={color} key={tag}>
                  {tag}
                </Tag>
              );
            })}
          </div>
        </div>
      </div>
      <div className={styles['article-item-img']}>
        <Image src={logoUrl} />
      </div>
    </div>
  );
};
