// import request from 'utils/request';
import originJsonp from 'jsonp';
const URL = {
  /* 歌曲vkey */
  songVkey: 'https://c.y.qq.com/base/fcgi-bin/fcg_music_express_mobile3.fcg',
};
//请求qq音乐接口的通用参数
const PARAM = {
  format: 'jsonp',
  inCharset: 'utf-8',
  outCharset: 'utf-8',
  notice: 0,
};
//原jsonp模块的配置项
// const OPTION = {
//   param: 'jsonpCallback',
//   prefix: 'callback',
// };
const jsonp = (url, data, option) => {
  return new Promise((resolve, reject) => {
    originJsonp(buildUrl(url, data), option, (err, data) => {
      if (!err) {
        resolve(data);
      } else {
        reject(err);
      }
    });
  });
};
//将obj类型的参数转为换url参数
function buildUrl(url, data) {
  let params = [];
  for (var k in data) {
    params.push(`${k}=${data[k]}`);
  }
  let param = params.join('&');
  if (url.indexOf('?') === -1) {
    url += '?' + param;
  } else {
    url += '&' + param;
  }
  return url;
}
export const CODE_SUCCESS = 0;

export default {
  //通过mid获取最新url / url会过期所要最新的
  getSong(songMid) {
    const data = {
      g_tk: 1278911659,
      hostUin: 0,
      platform: 'yqq',
      needNewCode: 0,
      cid: 205361747,
      uin: 0,
      songmid: songMid,
      filename: `C400${songMid}.m4a`,
      guid: 3655047200,
      ...PARAM,
    };
    const option = {
      param: 'callback',
      prefix: 'callback',
    };
    return jsonp(URL.songVkey, data, option);
  },
};
