import React, { useRef, useEffect } from 'react';
import styles from './index.module.scss';
interface IProps {
  progress: number;
  onClick: (progress: number) => void;
  onDrag: (progress: number) => void;
  onDrop: () => void;
}
const Progress = ({ progress, onClick, onDrag, onDrop }: IProps) => {
  const wrapperRef = useRef<HTMLDivElement>();
  const dotRef = useRef<HTMLDivElement>();
  const progressClick = (e) => {
    let left = wrapperRef.current.getBoundingClientRect().left;
    let width = wrapperRef.current.offsetWidth;
    let mouseX = e.clientX;
    let progress = (mouseX - left) / width;
    onClick(progress);
  };
  useEffect(() => {
    let clicked = false;
    dotRef.current.addEventListener('mousedown', (e) => {
      e.stopPropagation();
      clicked = true;
    });
    document.body.addEventListener(
      'mouseup',
      () => {
        if (clicked) {
          clicked = false;
          onDrop();
        }
      },
      false
    );
    document.body.addEventListener('mousemove', (e) => {
      if (clicked) {
        let width = wrapperRef.current.offsetWidth;
        let mouseX = e.clientX;
        let left = wrapperRef.current.getBoundingClientRect().left;
        let progress = (mouseX - left) / width;
        if (progress < 0) {
          progress = 0;
        }
        if (progress > 1) {
          progress = 1;
        }
        onDrag(progress);
      }
    });
  }, [onDrop, onDrag]);
  return (
    <div
      ref={wrapperRef}
      onMouseDown={progressClick}
      className={styles['wrapper']}
    >
      <div className={styles['wrapper-bottomline']}></div>
      <div
        style={{ width: `${progress * 100}%` }}
        className={styles['wrapper-topline']}
      ></div>
      <div
        style={{ marginLeft: `calc(${progress * 100}% - 8px)` }}
        className={styles['wrapper-dot']}
        ref={dotRef}
      ></div>
    </div>
  );
};
export default Progress;
