import { observable, action } from 'mobx';
import { songs } from 'const/music';
import api from 'api';
class MusicModel {
  @observable
  currentSong = {
    id: 4744710,
    mId: '000pi9Ow3CqeOa',
    name: '倾城',
    img:
      'http://y.gtimg.cn/music/photo_new/T002R300x300M000001GBgPY4XHcF6.jpg?max_age=2592000',
    duration: 255,
    url: '',
    singer: '许美静',
  };
  @observable
  songs = [];
  //是否播放过
  @observable
  played = false;
  @observable
  playing = false;
  //获取最新url
  @action
  getSongs = async () => {
    let promiseList = songs.map((item) => api.music.getSong(item.mId));
    Promise.all(promiseList).then((res: any) => {
      let result = res
        .map((item) => {
          if (item.code === 0) {
            return item.data.items[0];
          }
          return undefined;
        })
        .filter((v) => v);
      let existSongs = result
        .map((item) => {
          for (let i = 0; i < songs.length; i++) {
            if (item.songmid === songs[i].mId) {
              songs[
                i
              ].url = `http://dl.stream.qqmusic.qq.com/${item.filename}?vkey=${item.vkey}&guid=3655047200&fromtag=66`;
              return songs[i];
            }
          }
          return undefined;
        })
        .filter((v) => v);
      this.songs = existSongs;
      this.currentSong = songs[0];
    });
  };
  @action
  changePlaying = (status) => {
    this.playing = status;
  };
  @action
  changeSong = (id) => {
    this.currentSong = songs.find((song) => song.id === id) || ({} as any);
    this.changePlayed();
  };
  @action
  changePlayed = () => {
    this.playing = true;
    this.played = true;
  };
  @action
  prev = () => {
    let id = this.currentSong.id;
    let index = this.songs.findIndex((song) => song.id === id);
    let prevSong = songs[index - 1]
      ? songs[index - 1]
      : songs[songs.length - 1];
    this.changeSong(prevSong.id);
  };
  @action
  next = () => {
    let id = this.currentSong.id;
    let index = this.songs.findIndex((song) => song.id === id);
    console.log(index);
    let nextSong = songs[index + 1] ? songs[index + 1] : songs[0];
    this.changeSong(nextSong.id);
  };
}
export default new MusicModel();
