import { Provider } from 'mobx-react';
import DevTools from 'mobx-react-devtools';
import React, { useEffect, Fragment } from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import { config } from 'utils/request';
import { Redirect } from 'react-router-dom';
import 'antd/dist/antd.css';
import './App.css';
import SettingsStore from './stores/Settings';
import UserStore from './stores/User';
import music from './stores/music';
import Article from 'pages/Article';
import ArticleDetail from 'pages/Article/ArticleDetail';
import Music from 'pages/Music';
import BasicLayout from 'modules/basicLayout';

const user = new UserStore();
const settings = new SettingsStore();
config.url((url) => `${settings.apiServer}${url}`);
const App = () => {
  useEffect(() => {
    music.getSongs();
  }, []);
  return (
    <Provider user={user} settings={settings} music={music}>
      <Fragment>
        {process.env.NODE_ENV === 'development' ? <DevTools noPanel /> : null}
        <Router>
          <BasicLayout>
            <Switch>
              <Redirect from="/" to="/article" exact />
              <Route exact path="/article" component={Article} />
              <Route exact path="/article/detail" component={ArticleDetail} />
              <Route exact path="/music" component={Music} />
            </Switch>
          </BasicLayout>
        </Router>
      </Fragment>
    </Provider>
  );
};

export default App;
