import React, { useEffect, useCallback } from 'react';
import styles from './index.module.scss';
import { Icon } from 'components/antd';
import { songs } from 'const/music';
import musicStore from 'stores/music';
import classNames from 'classnames';
import { useLocalStore, observer } from 'mobx-react';
import api from 'api';
const Music = observer(() => {
  const fecthMusic = useCallback(async () => {
    let data = await api.music.getSong('000pi9Ow3CqeOa');
    console.log(data);
  }, []);
  useEffect(() => {
    fecthMusic();
  }, [fecthMusic]);
  const { changeSong, currentSong, playing } = useLocalStore(() => musicStore);
  return (
    <div className={styles.container}>
      <div className={styles['music']}>
        {songs.map(({ img, singer, name, id }) => (
          <div
            onClick={() => changeSong(id)}
            key={name}
            className={classNames(styles['music-item'], {
              [styles.active]: id === currentSong.id && playing,
            })}
          >
            <div className={styles['music-item-img']}>
              <img src={img} alt="-" />
            </div>
            <div className={styles['music-item-name']}>
              {singer}-{name}
            </div>
            <div className={styles['music-item-icon']}>
              {id === currentSong.id && playing ? (
                <Icon type="pause-circle" />
              ) : (
                <Icon type="play-circle" />
              )}
            </div>
          </div>
        ))}
      </div>
      <div className={styles['side']}></div>
    </div>
  );
});
export default Music;
