import React, { useRef, useState, useEffect } from 'react';
import styles from './index.module.scss';
import musicStore from 'stores/music';
import classNames from 'classnames';
import { useLocalStore, observer } from 'mobx-react';
import { message, Icon, Tooltip } from 'components/antd';
import Progress from '../Progress';
function processTime(time) {
  time = parseInt(time) || 0;
  let min =
    Math.floor(time / 60) > 9
      ? Math.floor(time / 60)
      : '0' + Math.floor(time / 60);
  let sec =
    Math.floor(time % 60) > 9
      ? Math.floor(time % 60)
      : '0' + Math.floor(time % 60);
  return `${min}:${sec}`;
}
const MusicPlayer = observer(() => {
  const [play, setPlay] = useState(false);
  const [firstPlay, setFirstPlay] = useState(true);
  const [progress, setProgress] = useState(0);
  const [{ currentTime }, setTime] = useState({
    duration: 0,
    currentTime: 0,
  });
  const [hideBar, setHideBar] = useState(true);
  const audioRef = useRef<HTMLAudioElement>();
  const ImgBoxRef = useRef<HTMLDivElement>();
  const playRef = useRef<boolean>();
  const progressRef = useRef<number>();
  playRef.current = play;
  progressRef.current = progress;
  const {
    currentSong: { name, img, singer, url, duration: originDuration },
    changePlayed,
    played,
    prev,
    next,
    changePlaying,
  } = useLocalStore(() => musicStore);

  //开始或暂停
  const playOrPause = (p?: number) => {
    try {
      const audio = audioRef.current;
      if (!play) {
        if (firstPlay) {
          audio.src = url;
          setFirstPlay(false);
        }
        //重暂停点击进度条播放开始
        audio.play();
        if (p) {
          //等待连接加载
          setTimeout(() => {
            audio.currentTime = audio.duration * p;
          }, 200);
        }
        changePlayed();
        setPlay(true);
        changePlaying(true);
      } else {
        audio.pause();
        setPlay(false);
        changePlaying(false);
      }
    } catch (e) {
      message.error('歌曲加载出错');
    }
  };
  //切换歌曲
  useEffect(() => {
    const audio = audioRef.current;
    if (played && audio) {
      setPlay(true);
      changePlaying(true);
      audio.src = url;
      audio.play();
    }
  }, [url, played, changePlaying]);
  //旋转或者暂停
  useEffect(() => {
    const imgBox = ImgBoxRef.current;
    if (play) {
      if (imgBox.className.indexOf(styles['active']) === -1) {
        imgBox.classList.add(styles['active']);
      }
      imgBox.style.webkitAnimationPlayState = 'running';
      imgBox.style.animationPlayState = 'running';
    } else {
      imgBox.style.webkitAnimationPlayState = 'paused';
      imgBox.style.animationPlayState = 'paused';
    }
  }, [play]);
  //点击进度条更改进度
  const updateProgress = (p) => {
    const audio = audioRef.current;
    if (audio) {
      if (play) {
        audio.currentTime = audio.duration * p;
      } else {
        playOrPause(p);
      }
    }
  };

  //拖动按钮修改进度
  const updateProgressByBtn = (p) => {
    const audio = audioRef.current;
    const play = playRef.current;
    if (audio && play) {
      setPlay(false);
      changePlaying(false);
      audio.pause();
    }
    setProgress(p);
  };
  //drop
  const drophandle = () => {
    const play = playRef.current;
    const audio = audioRef.current;
    if (!play && audio) {
      audio.currentTime = audio.duration * progressRef.current;
      setPlay(true);
      changePlaying(true);
      audio.play();
    }
  };

  //监听audio事件
  useEffect(() => {
    const audio = audioRef.current;
    audio.addEventListener('timeupdate', () => {
      if (playRef.current && audio) {
        let progress = audio.currentTime / audio.duration;
        setTime({ currentTime: audio.currentTime, duration: audio.duration });
        setProgress(progress);
      }
    });
    audio.addEventListener('error', () => {
      message.error('歌曲加载出错');
    });
    audio.addEventListener('ended', () => {
      next();
    });
  }, [next]);
  //监听键盘事件空格为暂停/播放
  useEffect(() => {
    const listener = (e) => {
      if (e.code === 'Space') {
        playOrPause();
      }
    };
    document.body.addEventListener('keyup', listener, false);
    return () => {
      document.body.removeEventListener('keyup', listener, false);
    };
  }, []);
  const hideOrShowPlaybar = () => {
    setHideBar((prev) => !prev);
  };
  return (
    <div
      className={classNames(styles['container'], {
        [styles['hide']]: hideBar,
      })}
    >
      <div className={'center-box'}>
        <div className={styles['innerbox']} id="player-innerbox">
          <div ref={ImgBoxRef} className={classNames(styles['innerbox-img'])}>
            <img src={img} alt={name} />
          </div>
          <div className={styles['innerbox-content']}>
            <div className={styles['progrees']}>
              <Progress
                progress={progress}
                onClick={updateProgress}
                onDrag={updateProgressByBtn}
                onDrop={drophandle}
              />
            </div>
            <div className={styles['info']}>
              <div>
                {singer} : {name}
              </div>
              <div style={{ marginLeft: 10 }}>
                {processTime(
                  currentTime >= originDuration ? originDuration : currentTime
                )}{' '}
                / {processTime(originDuration)}
              </div>
            </div>
          </div>
          <div className={styles['innerbox-handler']}>
            <div onClick={prev} title="切换上一首">
              <Icon type="step-backward" />
            </div>
            <div onClick={() => playOrPause()}>
              {play ? <Icon type="pause" /> : <Icon type="caret-right" />}
            </div>
            <div onClick={next} title="切换下一首">
              <Icon type="step-forward" />
            </div>
          </div>
        </div>
      </div>
      <audio autoPlay ref={audioRef}></audio>
      <Tooltip title={hideBar ? '展开播放器' : '收起播放器'}>
        <div
          className={classNames(styles['hide-show-btn'])}
          onClick={hideOrShowPlaybar}
        >
          {hideBar ? <Icon type="caret-up" /> : <Icon type="caret-down" />}
        </div>
      </Tooltip>
    </div>
  );
});
export default MusicPlayer;
