import React, { useState, useEffect, useCallback, lazy, Suspense } from 'react';
import { Tag, Button, message, Affix } from 'components/antd';
import { useHistory } from 'react-router-dom';
import { getUrlParams, debounce } from 'utils';
import { UTCtoDateString } from 'utils/date';
import api from 'api';
import { createArticle, Article } from 'models';
import styles from './ArticleDetail.module.scss';
import userInfo from 'const/userInfo';
import dict from 'const/dict';
import { articleCategoryToTag } from 'const/maps';
import { ReactComponent as Good } from 'assets/icon/good.svg';
import { ReactComponent as View } from 'assets/icon/view.svg';
import classNames from 'classnames';
import LazyLoading from 'components/LazyLoading';
import MarkNav from 'markdown-navbar';
import './mark-nav.scss';
const MarkDown = lazy(() => import('components/Markdown'));
export default () => {
  const [detail, setDetail] = useState<Article>(createArticle());
  const history = useHistory();
  const [id, articleCategory] = getUrlParams(['id', 'articleCategory']);
  const fetchDetail = useCallback(async () => {
    const { code, data } = await api.article.detail({ id });
    if (code === 10000) {
      setDetail(data);
    }
  }, [id]);
  const fetchStart = debounce(async () => {
    const { code } = await api.article.start({ id });
    if (code === 10000) {
      message.success('感谢点赞 ^_^ ');
    }
  });
  useEffect(() => {
    fetchDetail();
  }, [fetchDetail]);
  const { title, category, date, views, starts, content, wordsLength } = detail;
  return (
    <div className={styles['artilce-container']}>
      <div className={styles['artilce']}>
        <div className={styles['artilce-title']}>{title}</div>
        <div className={styles['artilce-head']}>
          <div className={styles['artilce-head-avatar']}>
            <img src={userInfo.avatarUrl} alt="avatar" />
          </div>
          <div className={styles['artilce-head-content']}>
            <div className={styles['artilce-head-content-name']}>
              {userInfo.username}
            </div>
            <div className={styles['artilce-head-content-info']}>
              <div className={styles['article-views']}>
                <View /> <span>{views}</span>
              </div>
              <div className={styles['article-zan']}>
                <Good /> <span>{starts}</span>
              </div>
              <div>字数 {wordsLength}</div>
              <div className={styles['article-date']}>
                更新时间： {UTCtoDateString(date)}
              </div>
            </div>
          </div>
          <div className={styles['artilce-head-sign']}>
            <Tag color={articleCategoryToTag[category]}>
              {dict.articleCategory[category]}
            </Tag>
          </div>
        </div>
        <div className={styles['artilce-content']}>
          <Suspense fallback={<LazyLoading />}>
            <MarkDown code={content} />
          </Suspense>
        </div>
        <div className={classNames(styles['artilce-footer'], 'btn-group')}>
          <Button onClick={fetchStart} icon={'like'} type="danger">
            点赞
          </Button>
          <Button
            onClick={() =>
              history.push(`/article?articleCategory=${articleCategory}`)
            }
          >
            返回
          </Button>
        </div>
      </div>
      <div className={styles['sidebar']}>
        <Affix offsetTop={80}>
          <MarkNav
            className="article-menu"
            source={content}
            headingTopOffset={60}
          />
        </Affix>
      </div>
    </div>
  );
};
